    -webkit-transform: skew(0.94deg);
    transform: skew(0.94deg);
  }

  15% {
    clip: rect(39px, 9999px, 2px, 0);
    -webkit-transform: skew(0.09deg);
    transform: skew(0.09deg);
  }

  20% {
    clip: rect(96px, 9999px, 40px, 0);
    -webkit-transform: skew(0.42deg);
    transform: skew(0.42deg);
  }

  25% {
    clip: rect(5px, 9999px, 41px, 0);
    -webkit-transform: skew(0.52deg);
    transform: skew(0.52deg);
  }

  30% {
    clip: rect(51px, 9999px, 35px, 0);
    -webkit-transform: skew(0.15deg);
    transform: skew(0.15deg);
  }

  35% {
    clip: rect(40px, 9999px, 82px, 0);
    -webkit-transform: skew(0.41deg);
    transform: skew(0.41deg);
  }

  40% {
    clip: rect(2px, 9999px, 18px, 0);
    -webkit-transform: skew(0.16deg);
    transform: skew(0.16deg);
  }

  45% {
    clip: rect(38px, 9999px, 34px, 0);
    -webkit-transform: skew(0.94deg);
    transform: skew(0.94deg);
  }

  50% {
    clip: rect(67px, 9999px, 66px, 0);
    -webkit-transform: skew(0.81deg);
    transform: skew(0.81deg);
  }

  55% {
    clip: rect(91px, 9999px, 33px, 0);
    -webkit-transform: skew(0.79deg);
    transform: skew(0.79deg);
  }

  60% {
    clip: rect(56px, 9999px, 56px, 0);
    -webkit-transform: skew(1deg);
    transform: skew(1deg);
  }

  65% {
    clip: rect(85px, 9999px, 80px, 0);
    -webkit-transform: skew(0.65deg);
