  }

  80% {
    clip: rect(72px, 9999px, 82px, 0);
    -webkit-transform: skew(0.34deg);
    transform: skew(0.34deg);
  }

  85% {
    clip: rect(84px, 9999px, 43px, 0);
    -webkit-transform: skew(0.32deg);
    transform: skew(0.32deg);
  }

  90% {
    clip: rect(64px, 9999px, 78px, 0);
